.xylophone{
    display: flex;
    align-items: center;
}
.note-button{
    padding: 10px;
    width: 60px;
    height: 200px;
    border: none;
    color: white;
    font-weight: bold;
    font-size: 24px;
    background-color: green;
    border-radius: 5px;
    transition: 0.3s;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    &:not(:last-child){
        margin-right: 15px;
    }
    &:hover{
        background-color: #2b6a2b ;
    }

    &:before,&:after{
        content: "";
        background: white;
        width: 15px;
        height: 15px;
        border-radius: 50%;
    }
}